import { call, put } from "redux-saga/effects";
import { ApiResponse } from "apisauce";

import { ACCESS_TOKEN_KEY, REFRESH_TOKEN_KEY } from "../../Constants/consts";
import API from "../utils/api";
import { logoutUser } from "../Reducers/authReducer";

type AnyResponse = ApiResponse<any>;

export default function* callCheckingAuth(api: any, ...rest: any) {
   const accessToken = localStorage.getItem(ACCESS_TOKEN_KEY) || ""; //       
   const refreshToken = localStorage.getItem(REFRESH_TOKEN_KEY) || "";

   const response: AnyResponse = yield call(api, accessToken, ...rest); //   (   ?)

      if (response.status === 401) {
        //      401 - 
         const { status: accessStatus } = yield call(
            API.verifyToken,
            accessToken
        ); //    accessToken

         if (accessStatus === 401) {
            // accessToken ,      refreshToken
            const { status: refreshStatus } = yield call(
               API.verifyToken,
               refreshToken
            ); //    refreshToken

            if (refreshStatus === 200) {
               const { ok, data } = yield call(
                  API.getNewAccessToken,
                  refreshToken
               ); //   accessToken

               if (ok && data) {
                  //     
                     const { access: newAccessToken } = data;
                     localStorage.setItem(ACCESS_TOKEN_KEY, newAccessToken); //   
                     const newResponse: AnyResponse = yield call(
                        api,
                        newAccessToken,
                  ...rest
                        ); //      
                           return newResponse; //   
               } else {
                  //     -      -   
                  yield put(logoutUser());
               }

            } else {
                  //   refreshToken -    
                  yield put(logoutUser());
            }

      } else {
            return response; // c   ,      
      }
      
   } else {
      return response; //     
   }
}